.app {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #c3dafe, #d6bcfa);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  padding: 2rem;
}

.card {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 10px 15px rgba(0,0,0,0.1);
  padding: 2rem;
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
}

.card h1 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1.5rem;
}

.controls {
  margin-bottom: 1.5rem;
  display: flex;
  gap: 0.5rem;
}

.input {
  flex: 1;
  padding: 0.75rem;
  border: 1px solid #cbd5e0;
  border-radius: 0.5rem;
  outline: none;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  font-weight: 500;
}

.btn.add {
  background: #5a67d8;
  color: #fff;
}

.btn.delete {
  background: #e53e3e;
  color: #fff;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.task-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  margin-bottom: 0.75rem;
  cursor: pointer;
  transition: background 0.2s;
  width: 100%;
  max-width: 350px;
}

.task-item:hover {
  background: #edf2f7;
}

.task-item.selected {
  background: #ebf4ff;
  border-color: #667eea;
}

.task-text span {
  word-break: break-word;
}

.btn.small {
  background: transparent;
  color: #718096;
  font-size: 0.875rem;
  padding: 0.25rem 0.5rem;
}

.empty {
  text-align: center;
  color: #718096;
}
